import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Pie } from 'react-chartjs-2';
import { Chart, ArcElement, Tooltip, Legend } from 'chart.js';

Chart.register(ArcElement, Tooltip, Legend);

function Tokenomics() {
  useEffect(() => {
    document.title = "Token Website | $LVN Token | Tokenomics";
  }, []);

  return (
    <div className="main-layout page-content tokenomics-page">
      {/* MAIN CONTENT */}
      <main className="page-content">
        {/* ===== HEADER ===== */}
          <div className="header-wrapper">
            <h1 className="header-title">
              <span className="header-symbol text-purple">$</span>
              <span className="text-teal">LVN</span>
            </h1>
            <span className="header-tagline text-purple">Tokenomics</span>
          </div>
          {/* ===== PARAGRAPH ===== */}
          <div className="paragraph-container">
            <p>
              <span className="text-teal">$</span><span className="text-purple">LVN</span> is designed to be a sustainable, long-term asset with a transparent and predictable tokenomics model. The goal is to create a fair and equitable distribution that rewards early adopters, interested parties, and holders while ensuring the longevity of the ecosystem.
            </p>
            <p>
              <span className="text-teal">$</span><span className="text-purple">LVN</span> tokens are unlocked following a predictable, transparent halving model every 4 years  no inflation surprises, no insider minting. This structure aligns with long-term believers and builders who understand that time and consistency create value.
            </p>
            <p>
              <span className="text-teal">$</span><span className="text-purple">LVN</span> is built with a fixed maximum supply and a Bitcoin-style halving mechanism to control inflation, increase adoption and provide transparency. Heres a breakdown of the token distribution:
            </p>
          </div>
          {/* ===== FLEXIBLE CHART CONTAINER ===== */}
          <div style={{ flex: 1, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', minHeight: 0, margin: '32px 0' }}>
            <div className="tokenomics-chart-container" style={{ width: '100%', maxWidth: 400, minWidth: 200, height: 'auto', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
              <PieChartLVN />
            </div>
          </div>
          {/* ===== BACK LINK ===== */}
          <div className="back-link-container" style={{ marginTop: '2.5rem', marginBottom: '3.5rem' }}>
            <Link to="/" className="back-link"> Back to Home</Link>
          </div>

        <div className="bottom-divider"></div>
      </main>
    </div>
  );
}

function PieChartLVN() {
  // Pie chart data for $LVN tokenomics (labels hidden, but shown in tooltip)
  const data = {
    labels: [
      'Halving Wallet (Locked)',
      'Liquidity Pool Wallet',
      'Staking Pool Wallet',
      'Airdrop Wallet (Locked)',
      'Developer Wallet',
      'Treasury Wallet',
      'Airdrop Wallet (Unlocked)',
      'Halving Wallet (Unlocked)'
    ],
    datasets: [
      {
        data: [4317601, 1000000, 1000000, 900000, 418001, 272533, 100000, 0],
        backgroundColor: [
          '#8e44ad',
          '#1abc9c',
          '#3498db',
          '#f1c40f',
          '#e67e22',
          '#e74c3c',
          '#2ecc71',
          '#bdc3c7'
        ],
        borderWidth: 1,
      },
    ],
  };
  const options = {
    plugins: {
      legend: { display: false },
      tooltip: {
        callbacks: {
          label: function(context) {
            const label = context.label || '';
            const value = context.parsed || 0;
            return `${label}: ${value.toLocaleString()}`;
          }
        }
      },
      maintainAspectRatio: true,
      responsive: true
    }
  };
  return (
    <div style={{ width: '90%', maxWidth: 400, minWidth: 120 }}>
      <Pie data={data} options={options} style={{ width: '100%', height: 'auto' }} />
    </div>
  );
}

export default Tokenomics;
