import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';

function About() {
  useEffect(() => {
    document.title = "Token Website | $LVN Token | About";
  }, []);

  return (
    <div className="main-layout page-content about-page">
      <main>
        {/* ===== HEADER ===== */}
        <div className="header-wrapper">
          <h1 className="header-title">
            <span className="header-symbol text-purple">$</span>
            <span className="text-teal">LVN</span>
          </h1>
          <span className="header-tagline text-purple">What is it?</span>
        </div>
        {/* ===== PARAGRAPH ===== */}
        <div className="paragraph-container">
          <p>
            <span className="text-teal">$</span><span className="text-purple">LVN</span> is a deflationary token built on the Polygon Mainnet, featuring a Bitcoin-style halving schedule and a focus on long-term growth. With a fixed supply of 8,008,135 tokens, $LVN offers transparent vesting and zero hidden mechanics.
          </p>
          <p>
            <span className="text-teal">$</span><span className="text-purple">LVN</span> was created with a long-term visionnot as a meme or for a quick pump, but as a serious foundation for growth we stand for transparency, community-first principles, and a predictable, sustainable supply schedule inspired by Bitcoins halving model.
          </p>
          <p>
            <span className="text-teal">$</span><span className="text-purple">LVN</span> isnt just another token; its a statement: real value is built on time, integrity, and a community that thinks in cycles, not hype. Whether youre here to build, invest, or contribute, youll be part of something designed to last.
          </p>
        </div>
        {/* ===== BACK LINK ===== */}
        <div className="back-link-container">
          <Link to="/" className="back-link"> Back to Home</Link>
        </div>
        <div className="bottom-divider"></div>
      </main>
    </div>
  );
}

export default About;
